<template>
	<div class="bubble flex-center" v-on:mouseenter="enter" v-on:mouseleave="over">
		<img src="..\assets\chat.png" alt="">
	</div>
</template>

<script>
export default {
	name: "Bubble",
	methods:{
		enter: function (event) {
			//let bubble = this.document.querySelector("bubble");
			event.target.style.backgroundColor = "#7800c8";
			event.target.firstChild.style.filter = "invert()";

			console.log(event.target.style.backgroundColor)
			
		},
		over: function (event) {
			//let bubble = this.document.querySelector("bubble");
			event.target.style.backgroundColor = "#f021ff";
			event.target.firstChild.style.filter = "";

			console.log(event.target.style.backgroundColor)
			
		}

	}
}
</script>

<style scoped>
.bubble{
	background-color: #f021ff;
	border-radius: 50%;
	height: 85px;
	width: 85px;
	transition: 0.5s;
}
img{
	width: 45px;
}
</style>