<template>
	<a class="button flex" v-on:mouseenter="enter" v-on:mouseleave="over">
		{{text}}
    <div class="arrow mt-7 ml-5"></div>
	</a>
</template>

<script>
export default {
	name: "Button",
	props: {
		text: String,
	},
	methods:{
		enter: function (event) {
			event.target.style.color = "rgb(216,40,255)";
			event.target.lastChild.classList.add("pink")
			//console.log(event.target.lastChild.style)
		},
		over: function (event) {
			event.target.style.color = "#fff";
			event.target.lastChild.classList.remove("pink")
      //console.log(event.target.style.backgroundColor)
    }
	}
};
</script>

<style scoped>

.button{
  width: 120px;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  color: white;
	font-weight: 600;
}
.arrow{
  width: 0; 
	height: 0;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 7px solid #fff;
}
.pink{
	border-top: 8px solid rgb(216,40,255);
}
</style>
