<template>

	<img :src="require(`@/assets/bubbles/${pix}`)" :id="src" class="bubble" />
	<!-- main -->
	<!-- <img v-if="src =='jpl.webp'" src="../assets/bubbles/jpl.webp" :id="src" class="bubble">

	what is
	<img v-if="src =='wpm.webp'" src="../assets/bubbles/wpm.webp" :id="src" class="bubble">

	manage
	<img v-if="src =='mpmt.webp'" src="../assets/bubbles/mpmt.webp" :id="src" class="bubble">
	<img v-if="src =='abrt.webp'" src="../assets/bubbles/abrt.webp" :id="src" class="bubble">

	Contact 
	<img v-if="src =='cbr.png'" src="../assets/bubbles/cbr.png" :id="src" class="bubble">
	<img v-if="src =='cpm.png'" src="../assets/bubbles/cpm.png" :id="src" class="bubble">
	
	Float 
	<img v-if="src =='float1.webp'" src="../assets/bubbles/float1.webp" :id="src" class="bubble">
	<img v-if="src =='float2.webp'" src="../assets/bubbles/float2.webp" :id="src" class="bubble">
	<img v-if="src =='float3.webp'" src="../assets/bubbles/float3.webp" :id="src" class="bubble">
	<img v-if="src =='float4.webp'" src="../assets/bubbles/float4.webp" :id="src" class="bubble"> -->

</template>

<script>
export default {
	name: 'bubble',
	props: {
		src: String,
		top: Number,
		left: Number,
		right: Number,
		width: Number,
		z: Number,
		float: Boolean
	},
	computed:{
		pix: function(){
			return this.src !== undefined ? this.src : 'jpl.webp'
		}
	},
	mounted(){
		const el = document.getElementById(this.src)
		el.style.top = this.top + "px"
		el.style.left = this.left + "vw"
		el.style.right = this.right + "vw"
		el.style.top = this.top + "px"
		el.style.width = this.width + "px"
		el.style.zIndex = this.z
	}

}
</script>

<style>
.bubble{
	position: absolute;
}
</style>